import NB from './nb.js'
var nickName = null
var avatar = null
export default {
	login() {
		var that = this
		var promise = new Promise(function(resolve, reject) {
			uni.showLoading({
				title: '加载中...',
				mask: false
			});

			wx.getUserProfile({
				desc: '获取您的用户信息', // 这个参数是必须的
				success: res => {
					var data = JSON.parse(res.rawData)
					that.nickName = data.nickName
					that.avatar = data.avatarUrl
					uni.login({
						provider: 'weixin',
						success: res => {
							that.getUnionid(res.code, resolve, 2)
						},
						fail(err) {
							uni.hideLoading()
						}
					})
				},
				fail() {
					uni.hideLoading()
				}
			})








































		})
		return promise;
	},
	getUnionid(code, resolve, type) {
		var that = this
		var url = ''
		if (type == 2) {
			url = getApp().globalData.mainurl + "App.Login.GetUnionid"
		} else {
			url = getApp().globalData.mainurl + "App.Login.GetUnionidByDy"
		}
		uni.request({
			url: url,
			method: 'POST',
			data: {
				'code': code,
			},
			success: function(res) {
				console.log(res)
				uni.hideLoading()
				if (res.data.data.errode) {
					uni.showToast({
						title: res.data.data.errmsg,
						icon: 'none'
					});
					return
				}
				if (type == 2) {
					uni.setStorage({
						key: 'openid',
						data: res.data.data.openid
					});
					that.thirdlogin(res.data.data.unionid, type, resolve)
				} else {
					uni.setStorage({
						key: 'openid',
						data: res.data.data.data.openid
					});
					that.thirdlogin(res.data.data.data.unionid, type, resolve)
				}
			},
			fail(err) {
				uni.hideLoading()
				console.log(err)
			}
		})
	},
	thirdlogin(openid, type, resolve) {
		uni.showLoading({
			title: '',
			mask: true
		});
		let sign = NB.sort2url({
			'openid': openid,
			'type': type
		});
		var dic = {}
		dic.nicename = this.nickName
		dic.avatar = this.avatar
		dic.openid = openid
		dic.type = type
		dic.sign = sign
		NB.sendRequest('Login.LoginByThird', dic).then(info => {
		
			uni.hideLoading()
			console.log(info)
			uni.showToast({
				title: '登录成功',
				icon: 'none'
			});
			this.saveLocal(info[0], resolve)
		})
	},
	saveLocal(info, resolve) {
		getApp().globalData.userinfo = info
		uni.setStorage({
			key: 'userinfo',
			data: info,
			success() {
				resolve(info)
			}
		});
	},
}
